<template>
  <div
    class="rocket"
    :style="{
      top: rocketY + 'px',
      left: rocketX + 'px',
      transform: `rotate(${rotation}deg)`
    }"
    ref="rocket"
    @touchmove="handleRocketTouchMove"
    @touchend="stopRocket"
  >
    <img style="width: 100px; height: 100px;" src="./SpaceRocket.svg" alt="SpaceRocket">
  </div>
</template>

<script>
export default {
  name: 'SpaceRocket',
  data() {
    return {
      rocketX: window.innerWidth / 2 - 50,
      rocketY: 60,
      rotation: 0,
      intervalId: null,
      rotationIntervalId: null,
      movingForward: false,
      movingBackward: false,
      movingLeft: false,
      movingRight: false,
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
    };
  },
  methods: {
    moveRocket(direction) {
      this.intervalId = setInterval(() => {
        const moveDistance = 5; // Distance to move each interval
        const angleInRadians = this.rotation * (Math.PI / 180); // Convert rotation to radians

        const moveX = Math.sin(angleInRadians) * moveDistance;
        const moveY = -Math.cos(angleInRadians) * moveDistance;

        if (direction === 'backward') {
          this.rocketX -= moveX;
          this.rocketY -= moveY;
        } else {
          this.rocketX += moveX;
          this.rocketY += moveY;
        }

        // Check rocket boundaries
        this.rocketX = Math.max(0, Math.min(this.rocketX, this.windowWidth - 100));
        this.rocketY = Math.max(0, Math.min(this.rocketY, this.windowHeight - 100));
      }, 10);
    },
    stopRocket() {
      clearInterval(this.intervalId);
      this.intervalId = null;
    },
    startRotation(direction) {
      const rotationSpeed = 1; // Rotation speed in degrees per interval

      this.rotationIntervalId = setInterval(() => {
        if (this.movingLeft && direction === 'left') {
          this.rotation -= rotationSpeed;
        } else if (this.movingRight && direction === 'right') {
          this.rotation += rotationSpeed;
        }
      }, 10);
    },
    stopRotation() {
      clearInterval(this.rotationIntervalId);
      this.rotationIntervalId = null;
    },
    handleKeyDown(event) {
      if (event.repeat) {
        return;
      }

      if (event.key === 'ArrowUp') {
        this.movingForward = true;
        this.moveRocket('forward');
      } else if (event.key === 'ArrowDown') {
        this.movingBackward = true;
        this.moveRocket('backward');
      } else if (event.key === 'ArrowLeft') {
        this.movingLeft = true;
        this.startRotation('left');
      } else if (event.key === 'ArrowRight') {
        this.movingRight = true;
        this.startRotation('right');
      }
    },
    handleKeyUp(event) {
      if (event.key === 'ArrowUp') {
        this.movingForward = false;
        this.stopRocket();
      } else if (event.key === 'ArrowDown') {
        this.movingBackward = false;
        this.stopRocket();
      } else if (event.key === 'ArrowLeft') {
        this.movingLeft = false;
        this.stopRotation();
      } else if (event.key === 'ArrowRight') {
        this.movingRight = false;
        this.stopRotation();
      }
    },
    handleRocketTouchMove(event) {
      const { rocket } = this.$refs;
      const touch = event.touches[0];
      const rocketRect = rocket.getBoundingClientRect();
      const rocketCenterX = rocketRect.left + rocketRect.width / 2;
      const rocketCenterY = rocketRect.top + rocketRect.height / 2;

      const moveX = touch.clientX - rocketCenterX;
      const moveY = touch.clientY - rocketCenterY;

      const angleInRadians = Math.atan2(moveX, -moveY);
      const angleInDegrees = angleInRadians * (180 / Math.PI);

      this.rocketX = Math.max(0, Math.min(this.rocketX + moveX, this.windowWidth - 100));
      this.rocketY = Math.max(0, Math.min(this.rocketY + moveY, this.windowHeight - 100));
      this.rotation = angleInDegrees;
    },
    updateWindowDimensions() {
      this.windowWidth = window.innerWidth;
      this.windowHeight = window.innerHeight;
    },
  },
  mounted() {
    document.addEventListener('keydown', this.handleKeyDown);
    document.addEventListener('keyup', this.handleKeyUp);
    window.addEventListener('resize', this.updateWindowDimensions);
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
    document.removeEventListener('keyup', this.handleKeyUp);
    window.removeEventListener('resize', this.updateWindowDimensions);
  },
};
</script>

<style scoped>
.rocket {
  position: absolute;
  width: 100px;
  height: 100px;
  z-index: 999;
}
</style>
