<template>
  <SphereContentView
    :selectedXspeed="selectedXspeed"
    :selectedYspeed="selectedYspeed"
    :selectedLang="currentLanguage"
  />
  <SpaceRocket />
  <ThemeMenu
    @isOpen="handleThemeMenuOpened"
    :isOpen="themeMenuOpen"
  >
    <LanguageMenu
      @languageChanged="changeLanguage"
      @isOpen="handleLanguageMenuOpened"
      :isOpen="langMenuOpen"
    />
    <SpeedMenu
      @speedChanged="handleSpeedChange"
      @isOpen="handleSpeedMenuOpened"
      :isOpen="speedMenuOpen"
    />
  </ThemeMenu>
</template>

<script>
import SphereContentView from '../../views/SphereContentView/index.vue';
import ThemeMenu from '../../components/DropdownMenu/ThemeMenu/index.vue';
import LanguageMenu from '../../components/DropdownMenu/LanguageMenu/index.vue';
import SpeedMenu from '../../components/DropdownMenu/SpeedMenu/index.vue';
import SpaceRocket from '../../components/SpaceRocket/index.vue';

export default {
  name: 'mainPage',
  components: {
    SphereContentView,
    ThemeMenu,
    LanguageMenu,
    SpeedMenu,
    SpaceRocket,
  },
  data() {
    return {
      selectedXspeed: 0.0001,
      selectedYspeed: 0.003,
      currentLanguage: 'en',
      langMenuOpen: false,
      speedMenuOpen: false,
      themeMenuOpen: false,
    };
  },
  methods: {
    handleSpeedChange(speed) {
      if (speed.type === 'x') {
        this.selectedXspeed = parseFloat(speed.value);
      } else if (speed.type === 'y') {
        this.selectedYspeed = parseFloat(speed.value);
      }
    },
    changeLanguage(language) {
      this.currentLanguage = language;
    },
    handleLanguageMenuOpened(isOpen) {
      this.langMenuOpen = isOpen;
      this.speedMenuOpen = false;
      this.themeMenuOpen = false;
    },
    handleSpeedMenuOpened(isOpen) {
      this.speedMenuOpen = isOpen;
      this.langMenuOpen = false;
      this.themeMenuOpen = false;
    },
    handleThemeMenuOpened(isOpen) {
      this.themeMenuOpen = isOpen;
      this.langMenuOpen = false;
      this.speedMenuOpen = false;
    },
  },
};
</script>
